import { dataComponent, dataElement } from '../app/_component'

const moveTitle = dataComponent('move-title')

if (moveTitle) {
    const titleText = dataElement('move-title:item')
    const titleContainer= dataElement('move-title:container')

    const originalParent = titleText.parentElement
    const originalNextSibling = titleText.nextElementSibling

    function handleResize() {
        if (window.innerWidth <= 992) {
            if (!titleContainer.contains(titleText)) {
                titleContainer.appendChild(titleText)
            }
        } else {
            if (
                originalParent &&
                originalNextSibling &&
                !originalParent.contains(titleText)
            ) {
                originalParent.insertBefore(titleText, originalNextSibling)
            } else if (originalParent && !originalParent.contains(titleText)) {
                originalParent.appendChild(titleText)
            }
        }
    }

    window.addEventListener('resize', handleResize)

    handleResize()
}
