export function dataComponent(value) {
    return document.querySelector(`[data-component="${value}"]`)
}

export function dataComponentAll(value) {
    return document.querySelectorAll(`[data-component="${value}"]`)
}

export function dataElement(value) {
    return document.querySelector(`[data-element="${value}"]`)
}

export function dataElementAll(value) {
    return document.querySelectorAll(`[data-element="${value}"]`)
}