import { dataComponent, dataElement, dataElementAll } from '../app/_component'

const infiniteScroll = dataComponent('infinite-scroll')

if (infiniteScroll) {
    const infiniteScrollAnimation = dataElement('infinite-scroll:animation')
    const infiniteScrollInner = dataElementAll('infinite-scroll:inner')

    document.addEventListener('DOMContentLoaded', () => {
        infiniteScrollInner.forEach((slider) => {
            if (infiniteScrollAnimation) {
                const innerWidth = infiniteScrollAnimation.scrollWidth
                const speed = 0.1
                const duration = Math.round(innerWidth / speed)
                slider.style.setProperty('--duration', `${duration}ms`)
            }
        })
    })
}
