import { dataComponent, dataElement } from '../../app/_component'

const stagesSliderComponent = dataComponent('stages-slider')

if (stagesSliderComponent) {
    const stagesSlider = dataElement('stages-slider:swiper')
    const buttonPrev = dataElement('stages-slider:prev')
    const buttonNext = dataElement('stages-slider:next')
    const paginationSlider = dataElement('stages-slider:pagination')

    new Swiper(stagesSlider, {
        slidesPerView: 1,

        pagination: {
            el: paginationSlider,
        },
        navigation: {
            nextEl: buttonNext,
            prevEl: buttonPrev,
        },
    })
}
