import { dataComponent, dataElement } from '../../app/_component'

const itemsSliderComponent = dataComponent('items-slider')

if (itemsSliderComponent) {
    const itemsSlider = dataElement('items-slider:swiper')
    const buttonPrev = dataElement('items-slider:prev')
    const buttonNext = dataElement('items-slider:next')
    const paginationSlider = dataElement('items-slider:pagination')

    new Swiper(itemsSlider, {
        slidesPerView: 3,
        grabCursor: true,
        spaceBetween: 20,
        loop: true,
        autoplay: {
            delay: 4000,
            disableOnInteraction: false,
        },

        pagination: {
            el: paginationSlider,
            type: 'fraction',
            renderFraction: function (currentClass, totalClass) {
                return `<span class="${currentClass}"></span> <span class="swiper-pagination-slash">/</span> <span class="${totalClass}"></span>`
            },
        },
        navigation: {
            nextEl: buttonNext,
            prevEl: buttonPrev,
        },
        breakpoints: {
            200: {
                slidesPerView: 1,
                spaceBetween: 0,
            },
            1200: {
                slidesPerView: 3,
                grabCursor: true,
                spaceBetween: 20,
                loop: true,
            },
        },
    })
}
